@charset "utf-8";
.language{
    font-size: 24px;
    color: var(--main_color);
}
.language>.language-select{
    position: absolute;
    width: 90px;
    text-align: center;
}
.btn{
    width: 100%;
}
.btn>button{
    font-size: 24px;
    color: #000;

}
.under{
    font-size: 14px;
}
.select{
    position: relative;
    z-index: 50;
}
.select>li>a{
    padding: 0;
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.5);
    z-index: 49;
}