@charset "utf-8";
@media (min-width:320px) and (max-width:1080px) {
    .countdown-wrap>h2 {
        font-size: 28px;
        margin: 60px 0 30px;
    }

    .countdown-wrap>div {
        font-size: 28px;
    }

    .countdown-wrap>div>.time {
        display: flex;
        gap: 15px;
        margin-bottom: 30px;
    }

    .isbirthday>h2 {
        font-size: 28px;
        margin-bottom: 30px;
    }

    .isbirthday>p {
        font-size: 20px;
    }
}